/* header */
.header {
  width: 100%;
  height: 56px;
  color: #fff;
  background: #4267b2;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.header_wrapper {
  position: relative;
  z-index: 1;
  padding-left: 20px;
  padding-right: 16px;
}
.header_inner {
  display: flex;
  align-items: center;
  height: 56px;
}

.header_logo {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 20px;
  margin: 8px 0;
  border-right: 2px solid rgba(255, 255, 255, 0.1);
}

.header_logo_img {
  margin-right: 10px;
}

.header_logo_title {
  font-size: 23px;
  font-weight: 600;
}

/* nav */
.nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-height: 56px;
}

.nav_item {
  display: flex;
  margin: 0;
  padding: 0;
}

/* nav_link */
.nav_link {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 6px 30px;
  list-style-type: none;
  cursor: pointer;
}

.nav_item--left .nav_link {
  background-color: transparent;
  transition: background-color 0.2s linear;
}

.nav_item--left .nav_link:hover {
  background-color: #35528e;
}

.nav_link_title {
  width: 100%;
  margin-top: 3px;
  font-size: 12px;
  font-weight: 400;
  line-height: 16, 34px;
  text-transform: capitalize;
  text-align: center;
}

.nav_item--right .nav_link {
  margin: 8px 0;
  padding: 8px 12px 8px 0;
}
.nav_item--right .nav_link:last-child {
  padding-right: 0;
}

.nav_item--right .nav_link--coins {
  padding-left: 0;
  margin: 8px 16px 8px 0;
  border-right: 2px solid rgba(255, 255, 255, 0.1);
}

.nav_link_coins {
  margin-top: 2px;
  margin-right: 10px;
}

.nav_link_counter_coins {
  font-size: 20px;
  line-height: 27, 24px;
  font-weight: 400;
}

/* nav_menu */

.nav_menu_item {
  display: none;
  position: absolute;
  top: 125%;
  z-index: 2;
  min-width: 134px;
  margin: 0;
  padding: 0;
  opacity: 0;
  background-color: #4267b2;
  border-radius: 4px 4px 0 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  transition: opacity 0.2s linear;
}
.menu_active {
  opacity: 1;
  z-index: 3;
}
.nav_menu_item:before {
  content: "";
  position: relative;
  top: -25px;
  right: -110px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #4267b2 transparent;
}

.nav_menu_item--right {
  right: 1%;
}
.nav_menu_item--left {
  right: 7%;
}
.menu_link_active {
  background-color: #35528e;
}

.menu_title {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px 16px;
}

.menu_title_img {
  margin-right: 8px;
}

.menu_title_text {
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  text-transform: capitalize;
}

.nav_menu_link {
  display: none;
  align-items: center;
  position: relative;
  top: -15px;
  z-index: 2;
  padding: 15px 20px;
  transition: background-color 0.2s linear;
}
.nav_menu_item--right .nav_menu_link {
  display: flex;
}
.nav_menu_item--right .nav_menu_link:first-child {
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.nav_menu_item--right .nav_menu_link:last-child {
  border-top: 2px solid rgba(255, 255, 255, 0.1);
}

.nav_menu_img {
  margin-right: 10px;
}

.nav_menu_title {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  color: #fff;
  text-transform: capitalize;
}

/* nav_menu_icon */

.nav_menu_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin: 6px 0;
  padding: 8px 14px;
  border-left: 2px solid rgba(255, 255, 255, 0.1);
}

.nav_menu_icon--more {
  margin: 0;
  padding: 6px 30px;
  border-left: none;
  background-color: transparent;
  transition: background-color 0.2s linear;
}
.nav_menu_icon--more:hover {
  background-color: #35528e;
}
.nav_menu_icon--mobile {
  padding-top: 12px;
}
.nav_menu_icon--profile {
  padding-right: 0;
}

.header_logo_title,
.nav_menu_title,
.nav_link_title,
.nav_menu_title,
.nav_menu_icon_title,
.menu_title_text {
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

#more,
#menu_icon,
#mobile,
#active_elem_menu {
  display: none;
}

/* media */

@media screen and (max-width: 1552px) {
  .nav_item--right {
    display: none;
  }
  /* .nav_menu_item, */
  #profile_menu,
  #menu_icon {
    display: flex;
  }
  .nav_menu_item--right {
    display: block;
  }
}

@media screen and (max-width: 1300px) {
  #more,
  #checks_menu {
    display: flex;
  }
  #checks,
  .header_logo_title {
    display: none;
  }
  .nav_menu_item--left {
    display: block;
  }
  .header_logo {
    padding-right: 8px;
  }
}

@media screen and (max-width: 1100px) {
  #feedback {
    display: none;
  }
  #feedback_menu {
    display: flex;
  }
}

@media screen and (max-width: 1000px) {
  #game {
    display: none;
  }
  #game_menu {
    display: flex;
  }
}

@media screen and (max-width: 900px) {
  #education {
    display: none;
  }
  #education_menu {
    display: flex;
  }
}

@media screen and (max-width: 800px) {
  #link {
    display: none;
  }
  #link_menu {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  #replacement {
    display: none;
  }
  #replacement_menu {
    display: flex;
  }
}

@media screen and (max-width: 500px) {
  #more,
  #task,
  #calendar,
  #staff {
    display: none;
  }
  #task_menu,
  #calendar_menu,
  #staff_menu {
    display: flex;
  }
  #mobile,
  #active_elem_menu {
    display: block;
  }
}
